.opacity-p-header {
  color: #ff001b;
  text-align: center;
  opacity: 0;
  font-size: 14px;
  font-weight: 400;
  transition: all .25s ease-in-out;
}

@media screen and (max-width: 430px) {
  .opacity-p-header {
    text-align: start;
  }
}

#inputSearch {
  width: 394px;
  color: #fff;
  background-color: #0000;
  border: none;
  border-bottom: .5px solid #fff;
  outline: none;
  padding: 5px 30px 5px 5px;
  transition: all .25s ease-in-out;
}

@media screen and (max-width: 430px) {
  #inputSearch {
    width: 100%;
  }
}

#inputSearch:focus {
  border-color: #ff6b08;
}

.header-form {
  width: 394px;
  margin-top: 40px;
  margin-bottom: 20px;
  position: relative;
}

@media screen and (max-width: 430px) {
  .header-form {
    width: 100%;
  }
}

.header-form label {
  pointer-events: none;
  color: #fff;
  transform-origin: 0 0;
  font-size: 14px;
  font-weight: 400;
  position: absolute;
  bottom: 5px;
  left: 5px;
}

.header-form input:focus + label {
  animation: .3s forwards labelFocus;
}

.header-form input:not(:focus) + label {
  animation: .3s forwards labelBlur;
}

@keyframes labelFocus {
  from {
    color: #fff;
    font-size: 14px;
    top: 0;
  }

  to {
    color: #666;
    font-size: .8em;
    top: -1.5rem;
  }
}

@keyframes labelBlur {
  from {
    color: #666;
    font-size: .8em;
    top: -1.5rem;
  }

  to {
    color: #fff;
    font-size: 14px;
    top: 2px;
  }
}

.header-form input:focus + button .search-icon path {
  fill: orange;
}

.header-form button {
  cursor: pointer;
  fill: #fff;
  width: 5px;
  height: 5px;
  background-color: #0000;
  border: none;
  position: absolute;
  bottom: 23px;
  right: 20px;
}

.div-input-header {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

